import { createAction } from "redux-actions";
import cFetch from "../../utils/cFetch";
import API from "../../config/api";
const { ENTRY } = API;

const getSomeMessageAction = createAction("COMMON_INFO"); //  action

// export const getSomeMessage = () => dispatch => {
