export const getSomeMessage = () => dispatch => {
  return cFetch(ENTRY.MESSAGE, { method: "GET" }).then(res => {
    console.log("", res);
    // store
    dispatch(getSomeMessageAction(res.result)); // dispatchaction
  });
};



